import React, { Component } from 'react';
import './App.css';
import Footer from './Components/Footer'
import Someextra from './Components/Someextra'
import {Spring, config} from 'react-spring/renderprops'
import Login from './Components/Login'
import fire from './config/Fire'
import Animate from './Components/Animate'
import { makeStyles } from '@material-ui/core/styles';
import BottomNavigation from '@material-ui/core/BottomNavigation';
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction';
import WhatsHot from '@material-ui/icons/Whatshot';
import Create from '@material-ui/icons/Create'
import AccountCircle from '@material-ui/icons/AccountCircleRounded'
import Home from '@material-ui/icons/Home'
import Background from './Components/Background'
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import DashBoardIcon from '@material-ui/icons/Dashboard';
import FavoriteIcon from '@material-ui/icons/Favorite';
import LocationOnIcon from '@material-ui/icons/LocationOn'
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import { BrowserRouter as Router,Route,Link} from "react-router-dom"
import Custom from './Components/Custom'
import Profile from './Components/Profile'
import Trend from './Components/Trend'
import Timeline from './Components/Timeline'

const styles = {
  root: {
    width: '100%',
    position: 'fixed',
    bottom: 0,
    background: '#eb1736',
  },
};

function Main() {
  return (
    <React.Fragment>
      <Background />
    </React.Fragment>
  )
}
function Find(){
  return (
    <React.Fragment>
      <Timeline />
    </React.Fragment>
  )
}
function Hot(){
  return (
    <React.Fragment>
      <Trend />
    </React.Fragment>
  )
}
function Pro(){
  return(
    <Profile />
  )
}

class App extends Component {
  state = {
    value: 'recents',
  };

  handleChange = (event, value) => {
    this.setState({ value });
  };
  Navi(){
    const { classes } = this.props;
    const { value } = this.state;
    return(
      <BottomNavigation value={value} onChange={this.handleChange} className={classes.root}>
          <Link to="/">
            <BottomNavigationAction label="Home" value="recents" icon={<Home />} />
          </Link>
          <Link to="/Create">
            <BottomNavigationAction label="Create" value="custom" icon={<Create />} />
          </Link>
          <Link to="/Trending">
            <BottomNavigationAction label="Hot" value="nearby" icon={<WhatsHot />} />
          </Link>
          <Link to="Profile">
            <BottomNavigationAction label="Profile" value="account" icon={<AccountCircle />} />
          </Link>
        </BottomNavigation>
    )
  }
  render() {
    

    return (
      <Router>
        
        <Route path="/" exact component={Main} />
        <Route path="/Create" component={Find} />
        <Route path="/Trending" component={Hot}/>
        <Route path="/Profile" component={Pro}/>
        {this.Navi()}
      </Router>
    )
  }
}
App.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(App);
