import React from 'react'
import ReactDOM from 'react-dom'
import { Parallax, ParallaxLayer } from 'react-spring/renderprops-addons'
import Moments from './Moments'
import PlaceSearch from './PlaceSearch'
import TextRand from './Animation/TextRand'
import PhotoD from './PhotoD'
import { Clock} from "grommet";
import Traveler from './Traveler'

export default class Background extends React.Component {
  render() {
    return (
      <Parallax ref={ref => (this.parallax = ref)} pages={3} >
        <ParallaxLayer offset={0} speed={1} style={
          { backgroundImage: "url(" + "https://images.pexels.com/photos/583846/pexels-photo-583846.jpeg?auto=compress&cs=tinysrgb&h=750&w=1260" + ")",
            backgroundPosition: 'center',
            backgroundSize: 'cover',
            backgroundRepeat: 'no-repeat' 
            }
        } />
        <ParallaxLayer offset={1} speed={1} style={
          { backgroundImage: "url(" + "https://images.pexels.com/photos/747964/pexels-photo-747964.jpeg?auto=compress&cs=tinysrgb&h=750&w=1260" + ")",
            backgroundPosition: 'center',
            backgroundSize: 'cover',
            backgroundRepeat: 'no-repeat' 
            }
        } />
        <ParallaxLayer offset={2} speed={1} style={
          { backgroundImage: "url(" + "https://images.pexels.com/photos/2178175/pexels-photo-2178175.jpeg?auto=compress&cs=tinysrgb&h=750&w=1260" + ")",
            backgroundPosition: 'center',
            backgroundSize: 'cover',
            backgroundRepeat: 'no-repeat' 
            }
        } />

        

        <ParallaxLayer offset={1.3} speed={-0.3} style={{ pointerEvents: 'none' }}>
          
        </ParallaxLayer>

        <ParallaxLayer offset={1} speed={0.8} style={{ opacity: 0.1 }}>
        </ParallaxLayer>

        <ParallaxLayer offset={1.75} speed={0.5} style={{ opacity: 0.1 }}>
        </ParallaxLayer>

        <ParallaxLayer offset={1} speed={0.2} style={{ opacity: 0.2 }}>
        </ParallaxLayer>

        <ParallaxLayer offset={1.6} speed={-0.1} style={{ opacity: 0.4 }}>
        </ParallaxLayer>

        <ParallaxLayer offset={2.6} speed={0.4} style={{ opacity: 0.6 }}>
        </ParallaxLayer>

        <ParallaxLayer offset={2.5} speed={-0.4} style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', pointerEvents: 'none' }}>
        </ParallaxLayer>

        
        <ParallaxLayer
          offset={0}
          speed={0.1}
          style={{ display: 'flex', justifyContent: 'center',color: '#fff',textShadow: '1px 2px 2px #000' }}
        >
            <TextRand value="Traveler" />
        </ParallaxLayer>
        <ParallaxLayer
          offset={0.8}
          speed={0.6}
        >
        <Clock 
        type="digital"
        size="xlarge" 
        alignSelf="center"
        style={{paddingLeft:120,color:'#fff',textShadow: '1px 2px 2px #000'}}  
        />
        </ParallaxLayer>
        <ParallaxLayer
          offset={0}
          speed={0.1}
          style={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
          <PlaceSearch />
        </ParallaxLayer>
        <ParallaxLayer
          offset={1}
          //speed={0.1}
          //onClick={() => this.parallax.scrollTo(2)}
          style={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
          <PhotoD />
        </ParallaxLayer>

        <ParallaxLayer
          offset={2}
          speed={-0}
          style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', }}>
          
        </ParallaxLayer>
      </Parallax>
    )
  }
}
