import React, { Component } from 'react';
import { Row,Col,Timeline, Icon,Select,Button,PageHeader,Typography } from 'antd';
import 'antd/dist/antd.css';
import {Trail} from 'react-spring/renderprops'

const {Option, OptGroup} = Select;
const {Paragraph} = Typography;

const routes = [
    {
        path: '/',
        breadcrumbName: 'First-Level'
    },
    {
        path: 'select',
        breadcrumbName: 'Second-Level'
    }
];


class Custom extends Component {
    state = {  }
    handleChange(value){
        console.log(`selected ${value}`);
    }
    content(){
        return(
            <div className="content">
                <Paragraph>
                Create your own trip!
                </Paragraph>
                <Paragraph>
                Some shit.
                </Paragraph>
                <p className="contentLink">
                <a>
                    <img
                    src="https://gw.alipayobjects.com/zos/rmsportal/MjEImQtenlyueSmVEfUD.svg"
                    alt="start"
                    />
                    Select pre built packages
                </a>
                </p>
            </div>
        )
    }
    render() { 
        return ( 
            <div>
                <PageHeader title="Traveler" breadcrumb={{ routes }}>
                    <div className="wrap">
                    <div className="content">{this.content()}</div>
                    </div>
                </PageHeader>
                <Row >
                    <Col span={12} offset={7}>
                        <Timeline mode="alternate" style={{marginTop:20}}>
                            <Timeline.Item>Flight from Kolkata 2019-06-01</Timeline.Item>
                            <Timeline.Item color="green">
                                Hotel checking in 2019-06-02
                            </Timeline.Item>
                            <Timeline.Item dot={<Icon type="clock-circle-o" style={{ fontSize: '16px' }} />}>
                            Sed ut perspiciatis unde omnis iste natus error sit voluptatem accusantium doloremque
                            laudantium, totam rem aperiam, eaque ipsa quae ab illo inventore veritatis et quasi architecto
                            beatae vitae dicta sunt explicabo. 2019-06-03
                            </Timeline.Item>
                            <Timeline.Item color="red">Hiking 2019-06-04</Timeline.Item>
                            <Timeline.Item dot={<Icon type="clock-circle-o" style={{ fontSize: '16px' }} />}>
                            Train Journey 2019-06-05
                            </Timeline.Item>
                        </Timeline>
                    </Col>
                </Row>
                <Row style={{marginBottom: 90}}>
                    <Col span={6} style={{marginRight: 10}}>
                        <Select defaultValue="Activity" style={{ width: 200 }} onChange={this.handleChange} >
                            <OptGroup label="Activity">
                            <Option value="Nachanachi">Nacho Nacho</Option>
                            <Option value="Palao">Bhago</Option>
                            </OptGroup>
                            <OptGroup label="Accommodation">
                            <Option value="Hotel">Hotel</Option>
                            <Option value="Transport">Transport</Option>
                            </OptGroup>
                        </Select>
                    </Col>
                    <Col span={6} offset={6}>
                            <Button type="primary" block>Add</Button>
                            <Button type="danger" block>Remove</Button>
                        
                    </Col>
                </Row>
            </div>
         );
    }
}
 
export default Custom;